<template>
    <Header />
    <sidebar />
    <p id="page_info_font">관리자페이지 > <strong>사원관리</strong></p>
    <div class="manage_search">
        <div class="manage_formSection">
            <form action="">
                <span id="tit_manage">
                    <h4>사원 검색</h4>
                </span>
                <select name="Number" id="manage_search_option" v-model="search_option">
                    <option value="Name">이름</option>
                    <option value="emp_num">사원 번호</option>
                    <option value="Position">직급</option>
                </select>
                <input type="text" name="search_txt" id="manage_Search_Input" v-model="search_keyword">
                <button id="search_btn" @click.prevent="user_search"><i
                        class="fa-solid fa-magnifying-glass"></i>&nbsp;&nbsp;검색</button>
            </form>
        </div>
    </div>
    <div class="manage_Table">
        <div class="manage_TableInfo">
            <h3 id="tit_manageTable">사원관리</h3>
            <form action="">
                <table id="employee_Table">
                    <tr>
                        <th>사&nbsp;&nbsp;&nbsp;&nbsp;번</th>
                        <th>이름</th>
                        <th>전화번호</th>
                        <th>부서명</th>
                        <th>직급</th>
                        <th colspan="2">가입승인 및 거부</th>
                        <th>수정</th>
                        <th>삭제</th>
                    </tr>
                    <tr v-for="user, index in user_info" :key="index">
                        <td id="in_empnum"><input type="text" name="user.emp_num" id="in_empnum" :value="user.emp_num"
                                readonly></td>
                        <td id="in_name"><input type="text" name="user.name" id="in_name" :value="user.name" readonly>
                        </td>
                        <td id="in_ph"><input type="text" name="user.phon" id="in_ph" :value="user.phon" readonly></td>
                        <td id="in_divi"><input type="text" name="user.division" id="in_divi" :value="user.division"
                                readonly></td>
                        <td id="in_Pos"><input type="text" name="user.Position" id="in_Pos" :value="user.Position"
                                readonly></td>
                        <td colspan="2">
                            <select name="approve" id="approve_table">
                                <option value="0" selected>--선택--</option>
                                <option value="Y">승인</option>
                                <option value="N">거부</option>
                            </select>
                        </td>
                        <td><button @click.prevent="UpdateFormOpen" :value="user.name">수정</button></td>
                        <td><button value="delete">삭제</button></td>
                    </tr>
                </table>
                <div class="admin_footer">
                    <button id="admin_submit_btn">저장</button>
                </div>
                <div class="slideFooter">
                    <v-pagination v-model="page" :length="this.user_info.length" total-visible="5" circle @click="pagePrev"></v-pagination>
                </div>
            </form>
            <input v-model="checkID"/>
            <button @click="check_id">테스트용 </button>
        </div>
    </div>
</template>

<script>
import Sidebar from '@/components/Sidebar.vue';
import Header from '@/components/Header.vue';
import router from '@/router/router';
export default {
    name: "manageForm",
    components: { Header, Sidebar, },
    data() {
        return {
            checkID:'',

            UpdateClick: false,
            user_info: [
                { emp_num: '002724', name: '김가윤', division: 'F/E개발팀', Position: '팀장', phon: '010-1178-3078', },
                { emp_num: '001724', name: '김청래', division: 'F/E개발팀', Position: '사원', phon: '010-1071-3024', },
                { emp_num: '012704', name: '고경훈', division: 'B/E개발팀', Position: '팀장', phon: '010-1558-1258', },
                { emp_num: '012770', name: '장건', division: 'B/E개발팀', Position: '대리', phon: '010-1121-3128', },
                { emp_num: '012774', name: '조호현', division: 'B/E개발팀', Position: '사원', phon: '010-1171-3075', },
                { emp_num: '002420', name: '하영진', division: 'F/E개발팀', Position: '사원', phon: '010-0078-3078', },
            ],
            updateIndex: 0,

            search_keyword: [],
            search_finish: false,
            search_result: [],
            search_option: [],
            user_approve: [],
            page: 1,

            val_id:"admin"
        }
    },
    methods: {
        check_id(){
            if (this.checkID == this.val_id){
                console.log("아이디 동일")
            } else {
                console.log("아이디 다름")
            }
        },






        user_search() {
            let option = this.search_option
            switch (option) {
                case 'Name':
                    for (let i = 0; i < this.user_info.length; i++) {
                        if (this.search_keyword == '') {
                            swal('키워드가 입력안됌')
                            break;
                        } else if (this.search_keyword.includes(this.user_info[i].name)) {
                            swal(this.search_keyword)
                            return this.search_finish = true;
                        } else if (this.search_keyword.includes(this.user_info[i].name)) {
                            swal('키워드 선택이 잘못되었거나, 해당 정보는 존재하지 않습니다.')
                            break
                        }
                    }
                    break
                case 'emp_num':
                    for (let i = 0; i < this.user_info.length; i++) {
                        if (this.search_keyword == '') {
                            swal('키워드가 입력안됌')
                            break;
                        } else if (this.search_keyword.includes(this.user_info[i].emp_num)) {
                            swal(this.search_keyword)
                            return this.search_finish = true;
                        } else if (!this.search_keyword.includes(this.user_info[i].emp_num)) {
                            swal('키워드 선택이 잘못되었거나, 해당 정보는 존재하지 않습니다.')
                            break
                        }
                    }
                    break
                case 'Position':
                    for (let i = 0; i < this.user_info.length; i++) {
                        if (this.search_keyword == '') {
                            swal('키워드가 입력안됌')
                            break;
                        } else if (this.search_keyword.includes(this.user_info[i].Position)) {
                            swal(this.search_keyword)
                            return this.search_finish = true;
                        } else if (!this.search_keyword.includes(this.user_info[i].Position)) {
                            swal('키워드 선택이 잘못되었거나, 해당 정보는 존재하지 않습니다.')
                            break
                        }
                    }
                    break
                default:
                    swal('검색조건을 선택해주세요.')
            }
        },
        pagePrev(){
            for (let i = 0; i < this.user_info.length; i++) {
            console.log(this.user_info[i].name)
                router.push({name:'manage',params:{
                    user_id: this.user_info[i].name
                }})
            }
        }

    }
}
</script>

<style lang="scss" scoped>
select {
    appearance: menulist
}

