    font-size: 14px;
    color: #808080;
    border-bottom: 1px solid #808080;
    width: 12rem;
    margin: 7.5rem 10rem 0rem 7.5rem;

    >strong {
        color: black;
    }
}

.manage_search {
    width: 50em;
    height: 30%;
    margin: 1rem 0.11rem 0.1rem 7rem;
    border-radius: 10px;
    padding: 1%;

    .manage_formSection {
        width: 70%;
        border-radius: 3.5px;
        min-height: auto;
        padding: 0.8rem;
        font-size: 18px;
        background-color: #fff;
        box-shadow: 0px 0px 3px #1e293b;

        >option {
            font-size: 0.3rem;
            background-color: black;
            color: #fff;
        }

        #tit_manage {
            >h4 {
                margin: 10px 0px 10px 0px;
                display: block;
                line-height: 1.5;
                width: 5rem;
            }
        }

        #manage_search_option {
            margin-right: 5px;
            line-height: 1.5;
            outline: none;
            border-radius: 4px;
            width: 7rem;
            text-align: center;
            border: 1px solid black;
        }

        #manage_Search_Input {
            border-radius: 3px;
            outline: none;
            border: 1px solid black;
            line-height: 1.3;
        }

        #search_btn {
            border: 1px solid black;
            border-radius: 4px;
            width: 7rem;
            margin-left: 10px;
            height: 5.5%;
            line-height: 1.6;
            font-size: 15px;
            outline: none
        }
    }
}

.manage_Table {
    margin: 1rem 1rem 1rem 7rem;
    width: 85%;
