    display: inline-block;
    justify-content: center;
    text-align: center;
    height: auto;

    .manage_TableInfo {
        padding: 0.3rem;
        background-color: #fff;
        border-radius: 10px;
        height: 5%;

        #tit_manageTable {
            text-align: center;
            padding: 5px;
        }

        #employee_Table {
            width: 95%;
            border-collapse: collapse;
