                width: 4rem;
            }

            #in_ph {
                width: 7.5rem;
                font-size: 100%;
            }

            #in_divi {
                width: 5.5rem;
                font-size: 100%;
            }

            #in_Pos {
                width: 6rem;
            }

            #approve_table {
                width: 5rem;
                border-radius: 2px;
                margin: 0 auto;
                text-align: center;
            }
        }

        th {
            border: 1px solid #808080;
            height: 25px;
            width: -1%;
            font-weight: 600;
        }

        td {
            border: 1px solid #808080;
            height: 30px;
            font-weight: 500;
            width: 10%;

            >input {
                text-align: center;
            }
        }

        button {
            border: 1px solid black;
            border-radius: 3px;
            width: 3.5rem;
            height: 1.5rem;
        }

        .admin_footer {
            #admin_submit_btn {
                text-align: center;
                width: 6rem;
                margin: 0 auto;
                justify-content: center;
                display: flex;
                margin-top: 30px;
                color: #fff;
                font-weight: 550;
                background-color: #1e293b;
                height: 2rem;
                padding: 5px;
                border-radius: 5px;
            }

            pagination-item-min-width{
                display: block;
                width: 100px;
                list-style: none;
                margin: 0 auto;
                position: relative;
                justify-content: center;
                overflow: hidden;
                top: 30px;
                >li {
                    border: 1px solid black;
                    width: 3%;
                    text-align: center;
                    padding: 5px;
                    border-radius: 3px;

                    &:hover {
                        background-color: #1e293b;
                        color: #fff;
                    }
                }

                #Pre {
                    width: 4%;
                    height: auto;
                    border: 1px solid black;
                    margin-right: 5px;
                    border-radius: 5px;
                    background-color: #1e293b;
                    color: #fff;
                }

                #Next {
                    width: 4%;
                    height: auto;
                    border: 1px solid black;
                    margin-left: 5px;
                    border-radius: 5px;
                    background-color: #1e293b;
                    color: #fff;
                }
            }
        }
    }

}
</style>